/**
 * System imports
 ============================================================================ */
import React from "react";
// import { Container, Row, Col } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
// import SubmitButton from "../../../submitButton";
// import Card from "../../../card";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "LeadsLogIn" };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {} = props.barrelGetters;
  const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Return
     ============================================================================ */
  return (
    <>
      <p className="article-title">
        Es aizmirsu savu paroli. Kā to atiestatīt?
      </p>
      <p>
        If you've forgotten your Local Currency password, but remember the email
        address you used to register with Local Currency, follow these steps to
        regain access to your account:
      </p>
      <ol className="article-ol">
        <li>
          Go to our <a href="/sign-in">Log In</a> page.
        </li>
        <li>Click Having trouble logging in?</li>
        <li>
          Enter the email address you use for Local Currency and click Next.
          (You'll be asked to confirm your identity by selecting a verification
          method.)
        </li>
        <li>
          Select how you want to recover your password, and click Next. (You'll
          be shown some options such as Have us call you, Receive a text,
          Receive an email, Answer your security questions, Confirm through
          Facebook Messenger or Confirm your identity using Google.)
        </li>
        <ul className="article-ul">
          <li>
            If you choose to Have us call you, you'll see a code on the screen.
            Use the code when you get our call. We'll then ask you to create a
            new password and confirm it by entering it a second time.
          </li>
          <li>
            If you choose to Receive a text, enter the 6-digit security code we
            send to your mobile device and click Next. We'll then ask you to
            create a new password and confirm it by entering it a second time.
          </li>
          <li>
            If you choose to Receive an email, enter the 6-digit security code
            we email to you and click Continue. We may ask you to confirm your
            card number. We'll then ask you to create a new password and confirm
            it by entering it a second time.
          </li>
          <li>
            If you choose to Answer security questions, enter your answers and
            click Continue. We may ask you to confirm your card number. We'll
            then ask you to create a new password and confirm it by entering it
            a second time.
          </li>
          <li>
            You may also be given the option to Confirm through Facebook
            Messenger. If you choose to use this method, click Send Code, enter
            the 6-digit security code we send to your Messenger and click
            Confirm. We'll then ask you to create a new password and confirm it
            by entering it a second time.
          </li>
          <li>
            If you have one or more Google email accounts registered to your
            Local Currency account, you may be required to go through an
            authorization challenge upon logging in to your Local Currency
            account. If you are already logged in to a Google account before the
            challenge step up, simply click Continue to complete the validation.
          </li>
        </ul>
      </ol>

      <p>
        ***Tip: your Local Currency password should be 8-20 characters long and
        include at least one number and one symbol (like !@#$%^).
      </p>
    </>
  );
};
